.overlay {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 20;
  background-color: rgba(0, 0, 0, .2);
  padding-left: 20px;
  padding-right: 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.modal {
  width: 280px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 30;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 30px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

.modal.active {
  transform: translate(-50%, -50%)scale(100%);
}

@media screen and (min-width: 768px) {
